import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    curUser: null,
    mainHome: 'https://fethipaediatrics.com/#/'
      // mainHome: 'http://localhost/fthcbtPaed/#/'
  },
  mutations: {
    curUser: (state, payload) => payload ? (state.curUser = {...payload }) : state.curUser = null,
  },
  actions: {
    setCurUser: ({ commit }, payload) => commit('curUser', payload),
  },
  modules: {}
});